import React, { useState, useEffect, useCallback } from 'react';
import { supabase } from '../supabaseClient';
import './ArticlesManagement.css';

//    
const SEARCH_OPTIONS = {
    NAME: 'name',
    ARTICLE_NUM: 'article_num',
    MATERIAL_TYPE: 'material_type'
};

const SORT_DIRECTION = {
    ASC: 'asc',
    DESC: 'desc'
};

function ArticlesManagement() {
    //   
    const [articles, setArticles] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    //     
    const [searchTerm, setSearchTerm] = useState('');
    const [searchBy, setSearchBy] = useState(SEARCH_OPTIONS.NAME);
    const [sortField, setSortField] = useState('article_id');
    const [sortDirection, setSortDirection] = useState(SORT_DIRECTION.ASC);

    //    
    const [modalState, setModalState] = useState({
        create: false,
        edit: false,
        delete: false
    });

    //   
    const [currentArticle, setCurrentArticle] = useState(null);
    const [formData, setFormData] = useState(getInitialFormData());

    //  
    function getInitialFormData() {
        return {
            name: '',
            article_num: '',
            thickness: '',
            material_type: '',
            file_url: ''
        };
    }

    //  
    const fetchArticles = useCallback(async () => {
        setLoading(true);
        setError(null);

        try {
            let query = supabase
                .from('articles')
                .select('*')
                .order(sortField, { ascending: sortDirection === SORT_DIRECTION.ASC });

            //  
            if (searchTerm) {
                query = query.ilike(searchBy, `%${searchTerm}%`);
            }

            const { data, error } = await query;
            if (error) throw error;

            setArticles(data || []);
        } catch (err) {
            console.error('  :', err);
            setError('   ');
        } finally {
            setLoading(false);
        }
    }, [searchTerm, searchBy, sortField, sortDirection]);

    useEffect(() => {
        fetchArticles();
    }, [fetchArticles]);

    //   
    const handleSort = (field) => {
        if (sortField === field) {
            setSortDirection(current =>
                current === SORT_DIRECTION.ASC ? SORT_DIRECTION.DESC : SORT_DIRECTION.ASC
            );
        } else {
            setSortField(field);
            setSortDirection(SORT_DIRECTION.ASC);
        }
    };

    //   
    const openModal = (type, article = null) => {
        setCurrentArticle(article);
        setModalState(prev => ({ ...prev, [type]: true }));

        if (type === 'create') {
            setFormData(getInitialFormData());
        } else if (type === 'edit' && article) {
            setFormData({
                name: article.name,
                article_num: article.article_num,
                thickness: article.thickness,
                material_type: article.material_type,
                    file_url: article.file_url || ''
            });
        }
    };

    const closeModal = (type) => {
        setModalState(prev => ({ ...prev, [type]: false }));
        setError(null);
    };

    //    
    const checkArticleNumberUnique = async (articleNum, excludeId = null) => {
        let query = supabase
            .from('articles')
            .select('article_num')
            .eq('article_num', articleNum);

        if (excludeId) {
            query = query.neq('article_id', excludeId);
        }

        const { data } = await query.single();
        return !data;
    };

    //   
    const handleSubmit = async (e, type) => {
        e.preventDefault();
        setLoading(true);
        setError(null);

        try {
            //      
            if (formData.article_num) {
                const isUnique = await checkArticleNumberUnique(
                    formData.article_num,
                    type === 'edit' ? currentArticle.article_id : null
                );

                if (!isUnique) {
                    throw new Error(`   ${formData.article_num}  !`);
                }
            }

            if (type === 'create') {
                const { error } = await supabase
                    .from('articles')
                    .insert([formData])
                    .select();
                if (error) throw error;

                alert('  !');
            } else if (type === 'edit') {
                const { error } = await supabase
                    .from('articles')
                    .update(formData)
                    .eq('article_id', currentArticle.article_id);
                if (error) throw error;

                alert('  !');
            }

            closeModal(type);
            fetchArticles();
        } catch (err) {
            console.error(` ${type === 'create' ? '' : ''} :`, err);
            setError(err.message);
        } finally {
            setLoading(false);
        }
    };

    //  
    const handleDelete = async () => {
        setLoading(true);
        setError(null);

        try {
            //   
            const { data: usageData } = await supabase
                .from('job_details')
                .select('job_detail_id')
                .eq('article_id', currentArticle.article_id)
                .limit(1);

            if (usageData?.length > 0) {
                throw new Error('         !');
            }

            const { error } = await supabase
                .from('articles')
                .delete()
                .eq('article_id', currentArticle.article_id);
            if (error) throw error;

            closeModal('delete');
            fetchArticles();
            alert('  !');
        } catch (err) {
            console.error('  :', err);
            setError(err.message);
        } finally {
            setLoading(false);
        }
    };

    //  
    const handleFormChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({
            ...prev,
            [name]: name === 'thickness' ? parseFloat(value) || '' : value
        }));
    };

    const handleClearSearch = () => {
        setSearchTerm('');
        setSearchBy(SEARCH_OPTIONS.NAME);
    };

    const renderSortIcon = (field) => {
        if (sortField !== field) return '';
        return sortDirection === SORT_DIRECTION.ASC ? '' : '';
    };

    const getSearchPlaceholder = () => {
        const placeholders = {
            [SEARCH_OPTIONS.NAME]: " ...",
            [SEARCH_OPTIONS.ARTICLE_NUM]: " ...",
            [SEARCH_OPTIONS.MATERIAL_TYPE]: " ..."
        };
        return placeholders[searchBy];
    };

    //  
    const renderTableHeader = () => (
        <thead>
        <tr>
            <th onClick={() => handleSort('article_id')}>
                ID {renderSortIcon('article_id')}
            </th>
            <th onClick={() => handleSort('article_num')}>
                 {renderSortIcon('article_num')}
            </th>
            <th onClick={() => handleSort('name')}>
                 {renderSortIcon('name')}
            </th>
            <th onClick={() => handleSort('thickness')}>
                 {renderSortIcon('thickness')}
            </th>
            <th onClick={() => handleSort('material_type')}>
                 {renderSortIcon('material_type')}
            </th>
            <th onClick={() => handleSort('file_url')}>
                 {renderSortIcon('file_url')}
            </th>
            <th></th>
        </tr>
        </thead>
    );

    const renderTableRow = (article) => (
        <tr key={article.article_id}>
            <td className="article-num">{article.article_id}</td>
            <td className="article-num">{article.article_num}</td>
            <td className="article-name">{article.name}</td>
            <td className="article-thickness">{article.thickness} </td>
            <td className="article-material">{article.material_type}</td>
            <td className="article-file">
                {article.file_url ? (
                    <a
                        href={article.file_url}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="file-link"
                    >
                         
                    </a>
                ) : (
                    <span className="no-file"></span>
                )}
            </td>
            <td className="actions">
                <button
                    className="edit-button"
                    onClick={() => openModal('edit', article)}
                    title=""
                >
                    
                </button>
                <button
                    className="delete-button"
                    onClick={() => openModal('delete', article)}
                    title=""
                >
                    
                </button>
            </td>
        </tr>
    );

    const renderFormField = (label, name, type = 'text', required = true, props = {}) => (
        <div className="form-group">
            <label>{label} {required && '*'}</label>
            <input
                type={type}
                name={name}
                value={formData[name]}
                onChange={handleFormChange}
                required={required}
                {...props}
            />
        </div>
    );

    //  
    if (loading && articles.length === 0) {
        return <div className="loading"> ...</div>;
    }

    return (
        <div className="articles-management">
            {/*     */}
            <div className="articles-header">
                <h1>  </h1>
                <button
                    className="create-button"
                    onClick={() => openModal('create')}
                    disabled={loading}
                >
                    +  
                </button>
            </div>

            {error && <div className="error-message">{error}</div>}

            {/*   */}
            <div className="articles-search">
                <div className="search-filters">
                    <div className="filter-field">
                        <label htmlFor="search-by"> :</label>
                        <select
                            id="search-by"
                            value={searchBy}
                            onChange={(e) => setSearchBy(e.target.value)}
                        >
                            <option value={SEARCH_OPTIONS.NAME}> </option>
                            <option value={SEARCH_OPTIONS.ARTICLE_NUM}> </option>
                            <option value={SEARCH_OPTIONS.MATERIAL_TYPE}> </option>
                        </select>
                    </div>

                    <div className="filter-field">
                        <label htmlFor="search-term">:</label>
                        <input
                            id="search-term"
                            type="text"
                            value={searchTerm}
                            onChange={(e) => setSearchTerm(e.target.value)}
                            placeholder={getSearchPlaceholder()}
                        />
                    </div>

                    <button
                        type="button"
                        className="filter-clear"
                        onClick={handleClearSearch}
                        disabled={!searchTerm && searchBy === SEARCH_OPTIONS.NAME}
                    >
                        
                    </button>
                </div>

                <div className="search-results-count">
                    : {articles.length} 
                </div>
            </div>

            {/*   */}
            <div className="articles-table-container">
                <table className="articles-table">
                    {renderTableHeader()}
                    <tbody>
                    {articles.map(renderTableRow)}
                    </tbody>
                </table>

                {articles.length === 0 && !loading && (
                    <div className="no-results">
                        {searchTerm ? '  ' : ' '}
                    </div>
                )}
            </div>

            {/*    */}
            {modalState.create && (
                <Modal
                    title="  "
                    onClose={() => closeModal('create')}
                    onSubmit={(e) => handleSubmit(e, 'create')}
                    loading={loading}
                    submitText={loading ? '...' : ''}
                >
                    {renderFormField(' ', 'article_num', 'text', true, {
                        placeholder: '3000312.00.001'
                    })}
                    {renderFormField(' ', 'name', 'text', true, {
                        placeholder: ' '
                    })}
                    {renderFormField(' ()', 'thickness', 'number', true, {
                        step: "0.1",
                        min: "0.1"
                    })}
                    {renderFormField(' ', 'material_type', 'text', true, {
                        placeholder: ' 3'
                    })}
                    {renderFormField('URL ', 'file_url', 'text', false, {
                        placeholder: 'design/file_name.dxf'
                    })}
                </Modal>
            )}

            {/*    */}
            {modalState.edit && (
                <Modal
                    title=" "
                    onClose={() => closeModal('edit')}
                    onSubmit={(e) => handleSubmit(e, 'edit')}
                    loading={loading}
                    submitText={loading ? '...' : ''}
                >
                    {renderFormField(' ', 'article_num')}
                    {renderFormField(' ', 'name')}
                    {renderFormField(' ()', 'thickness', 'number', true, {
                        step: "0.1",
                        min: "0.1"
                    })}
                    {renderFormField(' ', 'material_type')}
                    {renderFormField('URL ', 'file_url', 'text', false)}
                </Modal>
            )}

            {/*    */}
            {modalState.delete && currentArticle && (
                <Modal
                    title=" "
                    onClose={() => closeModal('delete')}
                    onSubmit={handleDelete}
                    loading={loading}
                    submitText={loading ? '...' : ', '}
                    type="delete"
                >
                    <p> ,    ?</p>
                    <div className="article-preview">
                        <strong>{currentArticle.article_num}</strong> - {currentArticle.name}
                    </div>
                </Modal>
            )}
        </div>
    );
}

//   Modal   
function Modal({
                   title,
                   children,
                   onClose,
                   onSubmit,
                   loading,
                   submitText,
                   type = 'form'
               }) {
    return (
        <div className="modal-overlay">
            <div className={`modal-content ${type === 'delete' ? 'delete-modal' : ''}`}>
                <h2>{title}</h2>
                {type === 'form' ? (
                    <form onSubmit={onSubmit}>
                        {children}
                        <div className="modal-actions">
                            <button type="submit" className="submit-button" disabled={loading}>
                                {submitText}
                            </button>
                            <button type="button" className="cancel-button" onClick={onClose}>
                                
                            </button>
                        </div>
                    </form>
                ) : (
                    <>
                        {children}
                        <div className="modal-actions">
                            <button className="delete-confirm-button" onClick={onSubmit} disabled={loading}>
                                {submitText}
                            </button>
                            <button className="cancel-button" onClick={onClose}>
                                
                            </button>
                        </div>
                    </>
                )}
            </div>
        </div>
    );
}

export default ArticlesManagement;