// components/NotificationCenterDB.js
import React, { useState, useEffect, useCallback, useRef } from 'react';
import { supabase } from '../../supabaseClient';
import './NotificationCenter.css';

const NotificationCenterDB = () => {
    const [notifications, setNotifications] = useState([]);
    const [unreadCount, setUnreadCount] = useState(0);
    const [loading, setLoading] = useState(true);
    const [isOpen, setIsOpen] = useState(false);
    const [connectionStatus, setConnectionStatus] = useState('disconnected');
    const dropdownRef = useRef(null);

    //  
    const loadNotifications = useCallback(async () => {
        try {
            setLoading(true);
            console.log('  ...');

            const { data, error } = await supabase
                .from('notifications')
                .select('*')
                .order('created_at', { ascending: false })
                .limit(50);

            if (error) throw error;

            if (data) {
                setNotifications(data);
                const unread = data.filter(n => !n.is_read).length;
                setUnreadCount(unread);
                console.log(`  ${data.length} `);
            }
        } catch (error) {
            console.error('   :', error);
        } finally {
            setLoading(false);
        }
    }, []);

    // Real-time 
    useEffect(() => {
        console.log('   Real-time...');

        //   
        loadNotifications();

        //  real-time 
        const subscription = supabase
            .channel('notifications-realtime')
            .on(
                'postgres_changes',
                {
                    event: 'INSERT',
                    schema: 'public',
                    table: 'notifications'
                },
                (payload) => {
                    console.log('     :', payload.new);
                    handleNewNotification(payload.new);
                }
            )
            .on(
                'postgres_changes',
                {
                    event: 'UPDATE',
                    schema: 'public',
                    table: 'notifications'
                },
                (payload) => {
                    console.log('  :', payload.new);
                    handleUpdatedNotification(payload.new);
                }
            )
            .on(
                'postgres_changes',
                {
                    event: 'DELETE',
                    schema: 'public',
                    table: 'notifications'
                },
                (payload) => {
                    console.log('  :', payload.old);
                    handleDeletedNotification(payload.old.id);
                }
            )
            .subscribe((status) => {
                console.log('  Real-time :', status);
                setConnectionStatus(status);

                if (status === 'SUBSCRIBED') {
                    console.log(' Real-time  ');
                } else if (status === 'CHANNEL_ERROR') {
                    console.error('   Real-time');
                }
            });

        return () => {
            console.log('   Real-time');
            subscription.unsubscribe();
        };
    }, [loadNotifications]);

    //   
    const handleNewNotification = useCallback((notification) => {
        setNotifications(prev => {
            // ,      
            const exists = prev.find(n => n.id === notification.id);
            if (exists) return prev;

            //      
            return [notification, ...prev];
        });

        if (!notification.is_read) {
            setUnreadCount(prev => prev + 1);

            //   
            if ('Notification' in window && Notification.permission === 'granted') {
                new Notification(notification.title, {
                    body: notification.message,
                    icon: '/favicon.ico'
                });
            }
        }
    }, []);

    //   
    const handleUpdatedNotification = useCallback((updatedNotification) => {
        setNotifications(prev =>
            prev.map(notification =>
                notification.id === updatedNotification.id ? updatedNotification : notification
            )
        );

        //   
        if (updatedNotification.is_read) {
            setUnreadCount(prev => Math.max(0, prev - 1));
        }
    }, []);

    //   
    const handleDeletedNotification = useCallback((notificationId) => {
        setNotifications(prev => {
            const deletedNotification = prev.find(n => n.id === notificationId);
            if (deletedNotification && !deletedNotification.is_read) {
                setUnreadCount(prev => Math.max(0, prev - 1));
            }
            return prev.filter(notification => notification.id !== notificationId);
        });
    }, []);

    //     
    useEffect(() => {
        const handleClickOutside = (event) => {
            if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
                const notificationButton = document.querySelector('.notification-button');
                if (notificationButton && !notificationButton.contains(event.target)) {
                    setIsOpen(false);
                }
            }
        };

        if (isOpen) {
            document.addEventListener('mousedown', handleClickOutside);
            document.addEventListener('touchstart', handleClickOutside);
        }

        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
            document.removeEventListener('touchstart', handleClickOutside);
        };
    }, [isOpen]);

    //   Escape
    useEffect(() => {
        const handleEscapeKey = (event) => {
            if (event.key === 'Escape' && isOpen) {
                setIsOpen(false);
            }
        };

        if (isOpen) {
            document.addEventListener('keydown', handleEscapeKey);
        }

        return () => {
            document.removeEventListener('keydown', handleEscapeKey);
        };
    }, [isOpen]);

    //     
    const markAsRead = async (notificationId = null) => {
        try {
            if (notificationId) {
                const { error } = await supabase
                    .from('notifications')
                    .update({ is_read: true })
                    .eq('id', notificationId);

                if (error) throw error;
                console.log('    :', notificationId);
            } else {
                const { error } = await supabase
                    .from('notifications')
                    .update({ is_read: true })
                    .eq('is_read', false);

                if (error) throw error;
                console.log('     ');
                setUnreadCount(0);
            }
        } catch (error) {
            console.error('      :', error);
        }
    };

    const deleteNotification = async (notificationId) => {
        try {
            const { error } = await supabase
                .from('notifications')
                .delete()
                .eq('id', notificationId);

            if (error) throw error;
            console.log('  :', notificationId);
        } catch (error) {
            console.error('    :', error);
        }
    };

    const clearAllNotifications = async () => {
        try {
            const { error } = await supabase
                .from('notifications')
                .delete()
                .gte('id', 0); //   

            if (error) throw error;

            setNotifications([]);
            setUnreadCount(0);
            console.log('   ');
        } catch (error) {
            console.error('    :', error);
        }
    };

    const handleNotificationClick = async (notification) => {
        //     
        if (!notification.is_read) {
            await markAsRead(notification.id);
        }

        //   
        if (notification.job_id) {
            window.location.href = `#/view/jobs?id=${notification.job_id}`;
        }

        setIsOpen(false);
    };

    const formatTime = (timestamp) => {
        const date = new Date(timestamp);
        const now = new Date();
        const diffMs = now - date;
        const diffMins = Math.floor(diffMs / 60000);
        const diffHours = Math.floor(diffMs / 3600000);
        const diffDays = Math.floor(diffMs / 86400000);

        if (diffMins < 1) return '';
        if (diffMins < 60) return `${diffMins}  `;
        if (diffHours < 24) return `${diffHours}  `;
        if (diffDays < 7) return `${diffDays}  `;

        return date.toLocaleDateString('uk-UA', {
            day: '2-digit',
            month: '2-digit',
            year: 'numeric'
        });
    };

    const getNotificationIcon = (type) => {
        switch (type) {
            case 'new_job': return '';
            case 'job_completed': return '';
            case 'system': return '';
            case 'warning': return '';
            case 'error': return '';
            default: return '';
        }
    };

    const getConnectionStatusText = () => {
        switch (connectionStatus) {
            case 'SUBSCRIBED': return ' ';
            case 'CHANNEL_ERROR': return ' ';
            case 'TIMED_OUT': return ' ';
            default: return ` '...`;
        }
    };

    if (loading && notifications.length === 0) {
        return (
            <div className="notification-center">
                <button className="notification-button" disabled>
                    
                </button>
            </div>
        );
    }

    return (
        <div className="notification-center">
            <button
                className="notification-button"
                onClick={() => setIsOpen(!isOpen)}
                title=""
            >
                
                {unreadCount > 0 && (
                    <span className="notification-badge">
                        {unreadCount > 9 ? '9+' : unreadCount}
                    </span>
                )}
            </button>

            {isOpen && (
                <div
                    className="notification-dropdown"
                    ref={dropdownRef}
                    onClick={(e) => e.stopPropagation()}
                >
                    <div className="notification-header">
                        <div>
                            <h3> </h3>
                            <div className="connection-status">
                                <div>: {notifications.length} </div>
                                <div>: {unreadCount}</div>
                                <div>
                                    : {getConnectionStatusText()}
                                </div>
                            </div>
                        </div>
                        <div className="notification-actions">
                            {unreadCount > 0 && (
                                <button onClick={() => markAsRead()}> </button>
                            )}
                            {notifications.length > 0 && (
                                <button onClick={clearAllNotifications}> </button>
                            )}
                            <button
                                className="close-dropdown-btn"
                                onClick={() => setIsOpen(false)}
                                title=""
                            >
                                
                            </button>
                        </div>
                    </div>

                    <div className="notification-list">
                        {notifications.length === 0 ? (
                            <div className="no-notifications">
                                  
                            </div>
                        ) : (
                            notifications.map((notification) => (
                                <div
                                    key={notification.id}
                                    className={`notification-item ${!notification.is_read ? 'unread' : ''}`}
                                >
                                    <div
                                        className="notification-content"
                                        onClick={() => handleNotificationClick(notification)}
                                    >
                                        <div className="notification-icon">
                                            {getNotificationIcon(notification.type)}
                                        </div>
                                        <div className="notification-text">
                                            <div className="notification-title">
                                                {notification.title}
                                            </div>
                                            <div className="notification-message">
                                                {notification.message}
                                            </div>
                                            <div className="notification-time">
                                                {formatTime(notification.created_at)}
                                                {notification.job_id && `   #${notification.job_id}`}
                                            </div>
                                        </div>
                                    </div>
                                    <button
                                        className="delete-notification-btn"
                                        onClick={(e) => {
                                            e.stopPropagation();
                                            deleteNotification(notification.id);
                                        }}
                                        title=" "
                                    >
                                        
                                    </button>
                                </div>
                            ))
                        )}
                    </div>

                    {notifications.length > 0 && (
                        <div className="notification-footer">
                            <div className="notification-stats">
                                    
                            </div>
                        </div>
                    )}
                </div>
            )}
        </div>
    );
};

export default NotificationCenterDB;